:active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}

.profileInfo {
    display: flex;
    align-items: center;
    justify-content: center;
}

.nameAboutMe {
    display: flex;
    position: relative;
    flex-direction: column;
    margin-left: 20px;
    max-height: 200px;
    max-width: 700px;
    hyphens: auto;
    word-wrap:break-word;
    overflow: auto;
}

.name {
    font-size: 25px;
    border-bottom: 1px solid #000;
}

.aboutMe {}

.avatar {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 1px solid black;
    box-shadow: 3px 3px 30px #022c24;
}

.wrapperImg {
    display: flex;
    justify-content: center;
    height: 200px;
    overflow: hidden;
    border-radius: 5px;
    border: 2px solid #022c24;
    margin-bottom: 10px;
    box-shadow: 3px 3px 30px #022c24;
}

.background {
    align-self: center;
    width: 100%;
    height: 100%;
}

.jobContainer {
    display: flex;
    justify-content: space-evenly;
    margin: 20px 0;
    font-size: 20px;
}

.readyForJob {
    max-width: 400px;
    hyphens: auto;
    word-wrap:break-word;
}

.lookingForAJob {
    font-size: 15px;
    border-bottom: 1px solid #000;
    padding-bottom: 10px;
}

.lookingForAJobImg {
    width: 20px;
}

.lookingForAJobDescription {
    margin-top: 10px;
}

.none {
    display: none;
    width: 0;
    height: 0;
}

.social {
    text-decoration: none;
    cursor: pointer;
    color: black;
}

.contacts {
    display: flex;
    flex-wrap: wrap;
    width: 300px;
    height: 150px;
    margin: auto 0;
}

.socialContainer {
    width: 150px;
    height: 40px;
}

.contactImg {
    position: relative;
    top: 5%;
    margin-right: 5px;
    width: 20px;
}

.currentStatus {
}

.newStatus {
    background-color: transparent;
    border: 1px solid #000;
    border-radius: 5px;
    min-height: 40px;
    max-height: 150px ;
    min-width: 100px;
    max-width: 700px;
    outline:none;
    box-shadow: 2px 2px 2px#022c24;
    padding-left: 10px;
    background-color: #004b3d;
    font-size: 15px;
    margin-top: 5px;
}

